import React,{useState,useEffect} from 'react'
import axios from 'axios'
import { useDispatch, useSelector } from 'react-redux'
import { bindActionCreators } from 'redux'
import { setMianChat } from '../../state/actionCreators'
import { useNavigate} from 'react-router-dom'
const MsgNotify = (props) => {
  axios.defaults.withCredentials=true
    const [profilePic, setProfilePic] = useState()
    const getProfileImage = async()=>{
        if(!props.userName){
            return
        }
        await axios.post(`/contact/contactprofileimage`,{userName:props.userName},{responseType: 'arraybuffer'}).then((resp)=>{
      
          let base64ImageString = Buffer.from(resp.data, 'binary').toString('base64')
          setProfilePic("data:image/png;base64,"+base64ImageString)
          
            }).catch((err)=>{
                console.log(err);
            })
        }
        useEffect(()=>{
          getProfileImage()
        },[])
        const navigate = useNavigate();
  let dispatch = useDispatch()
  const setMainChatfun= bindActionCreators(setMianChat,dispatch)
  const navigateToChat = ()=>{
    setMainChatfun({mainContact:props.userName})
    setTimeout(() => {
      navigate('/chat')
    }, 0);
  }
  return (
    <div className='MsgNotify' onClick={navigateToChat}>
        <div>
            <div>
              <img src={profilePic} alt={props.userName}/>
            </div>
            <div >
              <h5>{props.name}</h5>
              <p>@{props.userName}:{props.lastMessage}</p>
            </div>
        </div>
    </div>
  )
}

export default MsgNotify