import React, { useEffect, useRef, useState } from 'react';
import './styles/Chat/chatstyle.css';
import profileimg from '../assets/Frame 3.svg';
import InMessage from './chatSubCopenents/InMessage';
import OutMessage from './chatSubCopenents/OutMessage';
import sendArow from '../assets/send.svg';
import { useSelector } from 'react-redux';
import axios from 'axios';
//import io from 'socket.io-client';
import ScrollToBottom from 'react-scroll-to-bottom';
import RecentChat from './chatSubCopenents/RecentChat';

const SERVER = 'http://localhost:3001';

//const socket = io.connect();

const Chat = () => {
  axios.defaults.withCredentials = true;
  let [newMessage, setNewMessage] = useState({});
  let [myRMessages, setRMyMessages] = useState([]);
  let contactName = useSelector((data) => data.mainChat.mainContact);
  let userName = useSelector((data) => data.userInfo.userName);
  let chatUserName = useRef();
  const messageInput = useRef(null);
  /*
  useEffect(()=>{
    socket.emit("join_room", userName);
    socket.on("receive_message", (data) => {
      setNewMessage({message:data.message,sender:false ,author:data.author})
      
    });
  },[])*/

  useEffect(() => {
    if (newMessage.author == contactName) {
      setRMyMessages([
        ...myRMessages,
        { message: newMessage.message, sender: newMessage.sender },
      ]);
    }
  }, [newMessage]);

  const getAllMessages = async () => {
    if (contactName) {
      await axios
        .post(`/contact/getallmessages`, { withUser: contactName })
        .then((res) => {
          setRMyMessages(res.data.result);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };

  const sendMessage = async () => {
    let message = messageInput.current.value;
    if (message != '') {
      setRMyMessages([
        ...myRMessages,
        { message: message, sender: true },
      ]);
      messageInput.current.value = '';
      /*
      socket.emit('send_message', {
        room: contactName,
        author: userName,
        message: message,
      });*/
      await axios.post(`/contact/sendmessage`, {
        to: contactName,
        message: message,
      });
    }
  };
  const [profileImg, setProfileImg] = useState();
  const [recentChats, setRecentChats] = useState([]);
  const getRecentChats = async () => {
    await axios
      .get(`/contact/recentchats`)
      .then((res) => {
        let userID = res.data.userID;
        let ArrRecentChats = res.data.result.map((elem) => {
          return (
            <RecentChat
              name={elem.firstName + ' ' + elem.lastName}
              userName={elem.userName}
              message={elem.message}
              LastmsgSender={
                elem.senderID == userID ? 'You' : elem.firstName
              }
            />
          );
        });
        setRecentChats(ArrRecentChats);
      })
      .catch((err) => {
        console.log(err);
      });
    console.log('getRecentChats');
  };

  const getImage = async () => {
    if (!contactName) {
      return;
    } else {
      await axios
        .post(
          `/api/getprofilepic`,
          { userName: contactName },
          { responseType: 'arraybuffer' }
        )
        .then((res) => {
          console.log(res);
          let base64ImageString = Buffer.from(
            res.data,
            'binary'
          ).toString('base64');

          setProfileImg('data:image/png;base64,' + base64ImageString);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };
  useEffect(() => {
    setRMyMessages([]);
    getAllMessages();
    getImage();
  }, [contactName]);

  useEffect(() => {
    getRecentChats();
  }, []);
  if (!contactName) {
    return (
      <div className="lastMessages">
        <div>
          <h5>Recent Chats</h5>
        </div>
        <div>{recentChats}</div>
      </div>
    );
  } else {
    return (
      <div className="messagin">
        <div>
          <div>
            <img src={profileImg} />
            <p ref={chatUserName}>{contactName}</p>
          </div>
          <div>
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
        <ScrollToBottom>
          {myRMessages.map((message) => {
            if (message.sender) {
              return (
                <InMessage
                  message={message.message}
                  key={message.messageID}
                />
              );
            } else if (!message.sender) {
              return (
                <OutMessage
                  message={message.message}
                  userName={contactName}
                  key={message.messageID}
                />
              );
            }
          })}
        </ScrollToBottom>
        <div>
          <div>
            <input
              type="text"
              ref={messageInput}
              placeholder="Type Something..."
              onKeyUp={(e) => {
                if (e.key === 'Enter') {
                  sendMessage();
                }
              }}
            />
          </div>
          <div onClick={sendMessage}>
            <img src={sendArow} />
          </div>
        </div>
      </div>
    );
  }
};

export default Chat;
