import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useDispatch } from 'react-redux';
import { bindActionCreators } from 'redux';
import { setMianChat } from '../../state/actionCreators';

const RecentChat = (props) => {
  axios.defaults.withCredentials = true;
  let dispatch = useDispatch();
  const setMainChatfun = bindActionCreators(setMianChat, dispatch);
  const navigateToChat = () => {
    setMainChatfun({ mainContact: props.userName });
  };
  const [profilePic, setProfilePic] = useState();
  const getProfileImage = async () => {
    await axios
      .post(
        `/contact/contactprofileimage`,
        { userName: props.userName },
        { responseType: 'arraybuffer' }
      )
      .then((resp) => {
        let base64ImageString = Buffer.from(
          resp.data,
          'binary'
        ).toString('base64');
        setProfilePic('data:image/png;base64,' + base64ImageString);
      })
      .catch((err) => {
        console.log(err);
      });
  };
  useEffect(() => {
    getProfileImage();
  }, []);
  return (
    <div className="recentChat" onClick={navigateToChat}>
      <div>
        <img src={profilePic} />
      </div>
      <div>
        <h5>{props.name}</h5>
        <p>
          <span>{props.LastmsgSender}</span> : {props.message}
        </p>
      </div>
    </div>
  );
};

export default RecentChat;
