import axios from 'axios'
import React, { useEffect, useState } from 'react'
import CourseNotify from './CourseNotify'

const CourseNotification = () => {
  axios.defaults.withCredentials = true
  const [notification,setNotification] = useState([])
  const getNotification = ()=>{
    axios.get('/class/coursenotification').then((res)=>{
      setNotification(res.data.result)
    })
  }
  useEffect(()=>{
    getNotification()
  },[])
  return (
    <div>
      {
        notification.length?(
          notification.map((notify)=>{
            return <CourseNotify userName={notify.userName} name={notify.firstName+" "+notify.lastName} className={notify.className} />
          })
        ):("No notifications ")
      }
    </div>
  )
}

export default CourseNotification