import React, { useRef, useState, useEffect } from 'react';
import addPost from '../assets/addPost.svg';
import './styles/AddPost/addPost.css';
import postArrow from '../assets/postArrow.svg';
import plus from '../assets/+.svg';
import axios from 'axios';
import { useLocation } from 'react-router-dom';
import { useSelector } from 'react-redux';

const SERVER = 'http://localhost:3001';

const AddPost = (props) => {
  let name = useSelector((data) => data.userInfo.userName);

  const addPostDiv = useRef(null);
  const backgroundDiv = useRef(null);
  const content = useRef(null);

  const displayAddPost = () => {
    addPostDiv.current.style.display = 'flex';
    backgroundDiv.current.style.display = 'flex';
  };

  const undisplayAddPost = () => {
    addPostDiv.current.style.display = 'none';
    backgroundDiv.current.style.display = 'none';
  };
  const [image, setImage] = useState('');

  const saveIamge = (e) => {
    setImage({
      data: e.target.files[0],
      preview: URL.createObjectURL(e.target.files[0]),
    });
  };

  const createPost = async (e) => {
    const formData = new FormData();
    formData.append('image', image.data);
    formData.append('content', content.current.value);
    try {
      await axios
        .post(`/post/addpost`, formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
          },
        })
        .then((res) => {
          if (res.data.succes) {
            content.current.value = '';
            setImage({ data: '', preview: '' });
          }
        });
    } catch (ex) {
      console.log(ex);
    }
  };

  const [img, setImg] = useState(null);
  const getImage = async () => {
    await axios
      .post(
        `/api/getprofilepic`,
        { userName: name },
        { responseType: 'arraybuffer' }
      )
      .then((res) => {
        console.log(res);
        let base64ImageString = Buffer.from(
          res.data,
          'binary'
        ).toString('base64');

        setImg('data:image/png;base64,' + base64ImageString);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const location = useLocation();

  const setIsHome = () => {
    let AddPost = document.getElementById('addPost');
    switch (location.pathname) {
      case '/': {
        AddPost.style.display = 'flex';
        break;
      }
      case '/mainprofile': {
        AddPost.style.display = 'flex';
        break;
      }
      default: {
        AddPost.style.display = 'none';
        break;
      }
    }
  };
  useEffect(() => {
    getImage();
  }, []);
  useEffect(() => {
    setIsHome();
  }, [location.pathname]);

  return (
    <div id="addPost" className="b-addPost">
      <div onClick={displayAddPost}>
        <img src={addPost} />
      </div>
      <div className="addPost" ref={addPostDiv}>
        <div>
          <div>
            <img src={img} />
            <div>
              <h5>{name}</h5>
              <p>{props.role}</p>
            </div>
          </div>
          <div onClick={createPost}>
            <p>Post</p>
            <img src={postArrow} />
          </div>
        </div>
        <div>
          <textarea
            placeholder="Type something"
            ref={content}
          ></textarea>
        </div>
        <div>
          <img src={image.preview} />
        </div>
        <div>
          <img src={plus} />
          <input
            type="file"
            id="fileInput"
            placeholder="Attach file"
            onChange={(e) => {
              saveIamge(e);
            }}
          />
          <p>Attach file</p>
        </div>
      </div>
      <div
        className="background"
        ref={backgroundDiv}
        onClick={undisplayAddPost}
      ></div>
    </div>
  );
};

export default AddPost;
