import React, { useEffect, useRef } from 'react';
import {
  BrowserRouter as Router,
  Routes,
  Route,
} from 'react-router-dom';
import './App.css';

import profile from './assets/Frame 3.svg';

import { useSelector, useDispatch } from 'react-redux';
import axios from 'axios';
import { bindActionCreators } from 'redux';
import { insertUserInfo } from './state/actionCreators';

import Menu from './Compenents/Menu';
import Header from './Compenents/Header';
import ChatPanel from './Compenents/ChatPanel';
import AddPost from './Compenents/AddPost';
import Chat from './Compenents/Chat';
import NoPAge from './Compenents/NoPAge';
import Loading from './Compenents/homeSubCompenents/Loading';
import Preloader from './Compenents/GlobalSubCompenents/Preloader';
import LearningStudent from './Compenents/LearningStudent';

const HomeNotLogged = React.lazy(() =>
  import('./Compenents/HomeNotLogged')
);
const Settings = React.lazy(() => import('./Compenents/Settings'));
const Profile = React.lazy(() => import('./Compenents/Profile'));
const Learning = React.lazy(() => import('./Compenents/Learning'));
const Courses = React.lazy(() => import('./Compenents/Courses'));
const LearningTeacher = React.lazy(() =>
  import('./Compenents/LearningTeacher')
);
const Search = React.lazy(() => import('./Compenents/Search'));
const PostRouter = React.lazy(() =>
  import('./Compenents/PostRouter')
);

//import PostRouter from './Compenents/PostRouter';
//import Search from './Compenents/Search';
//import Home from './Compenents/Home';
//import HomeNotLogged from './Compenents/HomeNotLogged';
//import Settings from './Compenents/Settings';
//import Learning from './Compenents/Learning';
//import Profile from './Compenents/Profile';
//import Courses from './Compenents/Courses';
//import LearningTeacher from './Compenents/LearningTeacher';
const Home = React.lazy(() => import('./Compenents/Home'));

function App() {
  axios.defaults.withCredentials = true;
  const dispatch = useDispatch();
  const insertUserInf = bindActionCreators(insertUserInfo, dispatch);
  const checklogin = async () => {
    await axios
      .post(`/api/checklogin`)
      .then((res) => {
        if (res.data.succes) {
          insertUserInf({
            isLogged: true,
            userName: res.data.userName,
            student: res.data.student,
          });
        }
      })
      .catch(() => {
        insertUserInf({
          isLogged: false,
        });
      });
  };
  const b_home = useRef();

  useEffect(() => {
    checklogin();
  }, []);
  let isLogged = useSelector((data) => data.userInfo.isLogged);

  let student = useSelector((data) => data.userInfo.student);
  if (isLogged) {
    if (student == 1) {
      return (
        <React.Suspense fallback={<Loading />}>
          <div className="App">
            <Router>
              <div className="header">
                <Header />
              </div>
              <div className="content">
                <div className="b-menu">
                  <Menu student={true} />
                </div>
                <div className="b-home" ref={b_home}>
                  <Routes>
                    <Route path="/" element={<Home />}></Route>
                    <Route
                      path="/post/:postID"
                      element={<PostRouter />}
                    ></Route>
                    <Route path="/chat" element={<Chat />}></Route>
                    <Route path="/learning">
                      <Route path="" element={<Learning />}></Route>
                      <Route
                        path=":courseName"
                        element={<LearningStudent />}
                      ></Route>
                    </Route>
                    <Route
                      path="/setting"
                      element={<Settings />}
                    ></Route>
                    <Route
                      path="/search/:searched"
                      element={<Search />}
                    ></Route>
                    <Route
                      path="/mainprofile"
                      element={<Profile />}
                    ></Route>
                    <Route
                      path="/profile/:userName"
                      element={<Profile />}
                    ></Route>
                    <Route path="*" element={<NoPAge />}></Route>
                  </Routes>
                </div>
                <div className="chatPanel">
                  <ChatPanel />
                </div>
                <AddPost role={'Student'} image={profile} />
              </div>
            </Router>
          </div>
        </React.Suspense>
      );
    } else if (student == 0) {
      return (
        <React.Suspense fallback={<Loading />}>
          <div className="App">
            <Router>
              <div className="header">
                <Header />
              </div>
              <div className="content">
                <div className="b-menu">
                  <Menu student={false} />
                </div>
                <div className="b-home" ref={b_home}>
                  <Routes>
                    <Route path="/" element={<Home />}></Route>
                    <Route
                      path="/post/:postID"
                      element={<PostRouter />}
                    ></Route>
                    <Route path="/chat" element={<Chat />}></Route>
                    <Route path="/courses">
                      <Route path="" element={<Courses />}></Route>
                      <Route
                        path=":courseName"
                        element={
                          <LearningTeacher
                            myName="Edward Newgate"
                            module="Analyse"
                          />
                        }
                      ></Route>
                    </Route>
                    <Route
                      path="/setting"
                      element={<Settings />}
                    ></Route>
                    <Route
                      path="/search/:searched"
                      element={<Search />}
                    ></Route>
                    <Route
                      path="/mainprofile"
                      element={<Profile />}
                    ></Route>
                    <Route
                      path="/profile/:userName"
                      element={<Profile />}
                    ></Route>
                    <Route path="*" element={<NoPAge />}></Route>
                  </Routes>
                </div>
                <div className="chatPanel">
                  <ChatPanel />
                </div>
                <AddPost role={'Student'} image={profile} />
              </div>
            </Router>
          </div>
        </React.Suspense>
      );
    } else {
      return <Preloader />;
    }
  } else if (!isLogged && isLogged != undefined) {
    return (
      <React.Suspense fallback={<Preloader />}>
        <HomeNotLogged />
      </React.Suspense>
    );
  } else {
    return <Preloader />;
  }
}

export default App;
