import axios from 'axios'
import React, { useEffect, useState } from 'react'
import MsgNotify from './MsgNotify'

const MessageNotification = () => {
  axios.defaults.withCredentials=true
  const [unseenMessages,setunseenMessages] = useState([])
  const getMessageNotification = ()=>{
    axios.get('/contact/unseenMessages').then((res)=>{
      setunseenMessages(res.data.result)
    })
  }
  useEffect(()=>{
    getMessageNotification()
  },[])
  return (
    <div className='containerNotification'>
    {
      (unseenMessages.length!=0)?(
        unseenMessages.map((message)=>{
          return <MsgNotify userName={message.userName} lastMessage={message.message} name={message.firstName+" "+message.lastName} />
        })
      ):("No notification")
    }
    </div>
  )
}

export default MessageNotification