import React, { useRef, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import search_icon from '../../assets/search_icon.svg';

const Serch = () => {
  const navigate = useNavigate();
  const [serched, setSearched] = useState('');
  const searchInput = useRef();
  const navigateToSearch = () => {
    navigate(`/search/${serched}`);
  };
  return (
    <>
      <div>
        <div>
          <input
            type="text"
            placeholder="Serach here..."
            onChange={(e) => {
              setSearched(e.target.value);
            }}
            onKeyUp={(e) => {
              if (e.key == 'Enter') {
                navigateToSearch();
              }
            }}
          />
        </div>

        <Link to={`/search/${serched}`}>
          <img src={search_icon} />
        </Link>
      </div>
      <section className="searchPhone">
        <img
          src={search_icon}
          onClick={() => {
            if (searchInput.current.style.display == 'flex') {
              return (searchInput.current.style.display = 'none');
            }
            searchInput.current.style.display = 'flex';
          }}
        />
        <div ref={searchInput}>
          <input
            type="text"
            placeholder="Serach here..."
            onChange={(e) => {
              setSearched(e.target.value);
            }}
            onKeyUp={(e) => {
              if (e.key == 'Enter') {
                navigateToSearch();
              }
            }}
          />
        </div>
      </section>
    </>
  );
};

export default Serch;
