const initialState={ posts:[],firstPostID:1,lastPostID:2}


const Post = (state=initialState,action)=>{
    switch(action.type){
        case "Posts":
            state= action.payload;break;
        case "FirstPostID":
            state.firstPostID= action.payload;break;        
        case "LastPostID":
            state.firstPostID= action.payload;break;
        default : state=state;
    }
    return state;
}

export default Post;