import React, {
  useCallback,
  useEffect,
  useRef,
  useState,
} from 'react';
import useOutsideCom from '../GlobalSubCompenents/useOutsideCom';
import CourseNotification from './CourseNotification';
import MessageNotification from './MessageNotification';
import PostNotification from './PostNotification';

const Notification = (props) => {
  const [select, setSelect] = useState({
    messages: true,
    course: false,
    post: false,
  });
  const initialState = useRef({
    messages: false,
    course: false,
    post: false,
  });
  const notification = useRef();
  useOutsideCom(notification, props.notifImg);
  const handleClick = useCallback(() => {
    if (notification.current.style.display != 'flex') {
      return (notification.current.style.display = 'flex');
    } else return (notification.current.style.display = 'none');
  }, []);

  useEffect(() => {
    props.notifImg.current.addEventListener('click', handleClick);
  }, []);
  return (
    <div className="notification" ref={notification}>
      <div>
        <ul>
          <li
            className={select.messages ? 'cuurent' : ''}
            onClick={() => {
              setSelect({ ...initialState.current, messages: true });
            }}
          >
            Messages
          </li>
          <li
            className={select.course ? 'cuurent' : ''}
            onClick={() => {
              setSelect({ ...initialState.current, course: true });
            }}
          >
            Courses
          </li>
          <li
            className={select.post ? 'cuurent' : ''}
            onClick={() => {
              setSelect({ ...initialState.current, post: true });
            }}
          >
            Posts
          </li>
        </ul>
      </div>
      {select.messages ? <MessageNotification /> : ''}
      {select.course ? <CourseNotification /> : ''}
      {select.post ? <PostNotification /> : ''}
    </div>
  );
};

export default Notification;
