import React, { useEffect, useState } from 'react'
import arrow from "../assets/arrow.svg"
import profile from "../assets/Frame 3.svg"
import { useDispatch, useSelector } from 'react-redux'
import { bindActionCreators } from 'redux'
import { setMianChat } from '../state/actionCreators'
import { useNavigate} from 'react-router-dom'
import axios from 'axios'

const SERVER = "http://localhost:3001";

const LastChat = (props) => {
  axios.defaults.withCredentials = true
  const navigate = useNavigate();
  let dispatch = useDispatch()
  const [profilePic, setProfilePic] = useState()
  const setMainChatfun= bindActionCreators(setMianChat,dispatch)
  const navigateToChat = ()=>{
    setMainChatfun({mainContact:props.lastMessage})
    setTimeout(() => {
      navigate('/chat')
    }, 0);
  }
  const getProfileImage = async()=>{
  await axios.post(`/contact/contactprofileimage`,{userName:props.lastMessage},{responseType: 'arraybuffer'}).then((resp)=>{

    let base64ImageString = Buffer.from(resp.data, 'binary').toString('base64')
    setProfilePic("data:image/png;base64,"+base64ImageString)
    
      }).catch((err)=>{
          console.log(err);
      })
  }
  useEffect(()=>{
    getProfileImage()
  },[])
  return (
    <div className='lastChat' onClick={navigateToChat}>
      <div>
        <div>
              <img src={profilePic}/>
          </div>
          <div >
              <h5>{props.name}</h5>
              <p>@{props.lastMessage}</p>
          </div>
      </div>
        <div>
            <img src={arrow}/>
        </div>
    </div>
  )
}

export default LastChat