import React from 'react'
import { Link } from 'react-router-dom'
import moduleIcon from '../../assets/moduleIcon.svg'
const Module = (props) => {
  const copyToKeyboard = ()=>{
    navigator.clipboard.writeText(props.code)
  }
  return (
      <div className='b-module'>
        <Link to={props.module}>
          <img src={moduleIcon} />
        </Link>
        <div className='texts'>
            <h3 className='module-nama' >{props.module}</h3>
            <p className='class-code' title='Click to copy' onClick={copyToKeyboard}>Class code: <span className='code'>{props.code}</span> </p>
        </div>
      </div>
    
    
  )
}

export default Module