import React,{useState,useEffect} from 'react'
import '.././styles/Course/coursestyle.css'
import pdf from '../../assets/pdf.svg'
import download from '../../assets/download.svg'
import axios from 'axios'
import FileDownload from "js-file-download"
const Course = (props) => {
    axios.defaults.withCredentials=true
    const downloadCourse = ()=>{
        axios.post("/class/downloadcourse",{
          fileName: props.title
        },{responseType:"blob"}).then((res)=>{
            FileDownload(res.data,props.title)
            console.log("succefullyDownloaded");
        })
    }
  const [profilePic, setProfilePic] = useState()
    const getProfileImage = async()=>{
        if(!props.userName){
            return
        }
        await axios.post(`/contact/contactprofileimage`,{userName:props.userName},{responseType: 'arraybuffer'}).then((resp)=>{
      
          let base64ImageString = Buffer.from(resp.data, 'binary').toString('base64')
          setProfilePic("data:image/png;base64,"+base64ImageString)
          
            }).catch((err)=>{
                console.log(err);
            })
        }
        useEffect(() => {
          getProfileImage()
        }, [])
        
  return (
    <div className='contain'>
        <div className='informations'>
            <div className='top-left'>
                <img src={(props.profile)?(props.profile) :(profilePic) } className='profile'/>
                <div className='name-desc'>
                    <h2 className='name'>{props.name}</h2>
                    <p className='job'>Teacher</p>
                </div>
            </div>
            <h3 className='module'>{props.module}</h3>
        </div>
        
        <div className='post-desc'>
            <div className='text' >{props.text}</div>
            { props.title!="null" &&
                <div>
                <div className='contain pdf'>
                    <div className='left-side'>
                        <img src={pdf} className='icon'/>
                        <h4 className='title'>{props.title}</h4>
                    </div>
                    <img src={download} className='down' onClick={downloadCourse}/>
                </div>
                </div>
            } 
            
        </div>
    </div>
  )
}

export default Course