import React,{useState,useEffect, useRef} from 'react'
import Module from './learningSubCompenents/Module';
import Course from './GlobalSubCompenents/Course';
import axios from 'axios';
import { useParams } from 'react-router-dom'
import LeaveClass from './learningSubCompenents/LeaveClass';

const LearningStudent = (props) => {
const module = useParams().courseName
const [courseList, setcourseList] = useState({result:[]})
const [image, setImage] = useState()
const teacherUserName = useRef()
const getImage = async ()=>{
  if(!teacherUserName){
    return
  }
  await axios.post(`/contact/contactprofileimage`,{userName:teacherUserName.current},{responseType: 'arraybuffer'}).then((res)=>{
      console.log(res);
      let base64ImageString = Buffer.from(res.data, 'binary').toString('base64')

      setImage("data:image/png;base64,"+base64ImageString);
  }).catch((err)=>{
      console.log(err);
  })
}
  const getAllCourses =async ()=>{
    await axios.get("/class/allmodulecourses",{
      params:{
        className: module
      }
    }).then((res)=>{
      setcourseList({result:res.data.result,info:res.data.info})
      teacherUserName.current = res.data.info.userName
    })
  }

  const setCourseSeen = ()=>{
    axios.post('/class/setAllSeen',{className:module})
  }
  useEffect(() => {
    getAllCourses()
    setCourseSeen()
  }, [])
  useEffect(()=>{
    getImage().then(()=>{
      console.log(teacherUserName.current);
      console.log(image);
    })
    console.log("rien");
  },[teacherUserName.current])
  return (
    <div className='whole-pg'>
      <div className='flexit'>
        <h5 className='heading'>Learning</h5>
        <LeaveClass module={module}/>
      </div>
      
      <div className='postes'>
      {
          courseList.result[0] && (
            courseList.result.map((course)=>{
              return <Course userName={courseList.info.userName} name={courseList.info.firstName+" "+courseList.info.lastName} module={props.module} title={course.pdfName} text={course.content}/>

            })
          )
        }
        
      </div>
    </div>
  )
}

export default LearningStudent