import axios from 'axios'
import React, { useEffect } from 'react'

const OutMessage = (props) => {
  axios.defaults.withCredentials=true
  const setmessageseen = ()=>{
    if(!props.userName){
      return
    }
    axios.post('/contact/setmessageseen',{withUser:props.userName}).catch((err)=>{
      throw err
    })
  }
  useEffect(()=>{
    setmessageseen()
  },[])
  return (
    <div className='outMessage'> 
      <div>
        {props.message}
      </div>       
    </div>
  )
}

export default OutMessage